<template>
  <div class="home">
          <van-row class="nav">
            <van-col class="shopcart" span="24">
              购物车
              <van-button class="nav-but">管理</van-button>

            </van-col>
          </van-row>

          <van-col v-for="(item,index) in pros" :key='index' class="centent" span="24">
            <van-checkbox-group v-model="result" ref="checkboxGroup">
              <van-checkbox class="check" ></van-checkbox>
             </van-checkbox-group>
            <div class="cent-box">
              <div class="cent-img">
                <img class="img-auto" :src="item.url" alt="">
              </div>
              <div class="name">{{item.name}}</div>
              <div class="jiage">￥{{item.price}}</div>
              <div class="sl">数量：1</div>
              <van-button class="cent-but">删除</van-button>
              <div><van-stepper class="stepper"  integer /></div>

            </div>
          </van-col>



          <van-submit-bar
            :price="3050"
            button-text="提交订单"
            class="submit"
          >
            <van-checkbox class="checkbox" >全选</van-checkbox>
          </van-submit-bar>
  </div>
</template>

<style scoped lang="less">
    .nav{
      height:1.33rem;
      background-color: #e4393c;
      .shopcart{
          width: 100%;
          margin: 0 auto;
          text-align: center;
          line-height: 1.33rem;
          font-size: 0.58rem;
          padding: 0.02rem;
          color: #fff;
         .nav-but{
            float: right;
            height: 0.93rem;
            line-height: 0.93rem;
            margin-top:0.2rem;
            margin-right:0.1rem;
            background-color: #ccc;
          }
      }
    }

    .centent{
      width: 100%;
      height: 3rem;
      background-color:#ccc;
      margin: 0.26rem auto;
      position: relative;

     .check{
        display: block;
        position: absolute;
        left: 0.26rem;
        top: 1.2rem;
      }

      .cent-box{
        width:calc(100% - 1rem);
        height:3rem;
        display: block;
        position: absolute;
        left: 1rem;
        top: 0rem;
        background-color: #ccc;

         .cent-img{
          display: block;
          width: 2.13rem;
         position: absolute;
         top: 0.8rem;
         left: 0;

        }
        .name{
          position: absolute;
          display: block;
          top: 0.7rem;
          left:2.4rem;
          width:2.86rem;
          font-size: 0.48rem;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
        .jiage{
          position: absolute;
          display: block;
          top:1.6rem;
          left:2.4rem;
          font-size: 0.42rem;
        }
        .stepper{
          position: absolute;
          display: block;
          top:1.4rem;
          left:4rem;
        }
        .cent-but{
          position: absolute;
          top:1.3rem;
          right:0.1rem;
          height: 0.93rem;
          line-height: 0.93rem;
          background-color: #ccc;
          display: block;
        }

        .sl{
          position: absolute;
          display: block;
          font-size: 0.42rem;
          top:2.4rem;
          left:4rem;
        }
      }


    }


    .submit{
      background-color: #999;
    }
    .checkbox{
      margin-left: 0.1rem;
    }

</style>

<script>
  export default {
    data() {
      return {
        checked: false,
        pros:null,
        result: []
      };
    },
    created(){
    	let self = this;

    	//截取查询参数
    	let queryParams = this.$route.query;

    	this.http(queryParams).then(result =>{
    		self.pros = result;
    	})
    },

    methods:{


    	http(data){
    		return new Promise((resolve,reject) =>{
    			//发起ajax请求
    			//创建ajax对象
    			let xhr = new XMLHttpRequest();

    			xhr.onreadystatechange = function (){
    				if(this.readyState ===4 && this.status ===200){
    					//成功凝固
    					resolve(this.response);
    				}
    			}

    			let params = "";

    			for(let key in data) {
    				params += key + "=" + data[key] + "&";
    			}

    			params = params.slice(0,-1);

    			xhr.open('GET','http://api.kangliuyong.com:9002/pros?' + params,true);

    			//设置服务器响应数据类型
    			xhr.responseType = 'json';

    			xhr.send(null);

    		})
    	},


    }
  };
</script>
