import Vue from 'vue'
import App from './App.vue'
import router from './router'

// 从vant组件库导入Button组件
import {

  Button,
  Field,
  Row,
  Col,
  Stepper,
  SubmitBar,
  Checkbox,
  CheckboxGroup,

 } from 'vant'


// 根据屏幕适配rem
import 'lib-flexible/flexible'

// 全局注册组件
Vue
.use(Button)
.use(Field)
.use(Row)
.use(Col)
.use(Stepper)
.use(SubmitBar)
.use(Checkbox)
.use(CheckboxGroup)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
